import '../css/ContentInfo.css';
import { likeBtn, dislikeBtn, moreInfoBtn, playBtn, addBtn } from '../assets/SVGIcons';

function ContentInfo(props) {
    return <div id='item-info' onClick={() => props.loadMoreInfo(props.show)}>
        <div id='info-btns'>
            <button className='info-btn' style={{background: 'white', fill: 'black', border: '2px solid white'}}><div>{ playBtn }</div></button>
            <button className='info-btn'><div>{ addBtn }</div></button>
            <button className='info-btn'><div>{ likeBtn }</div></button>
            <button className='info-btn'><div>{ dislikeBtn }</div></button>
            <button className='info-btn end'><div>{ moreInfoBtn }</div></button>
        </div>
        <h5 id='info-compatibility'>98% Match</h5>
        <h5 id='info-seasons'>{ props.show.type === 'TV' 
            ? props.show.seasons
            : props.show.length }</h5>
        <br/>
        <ul id='tag-list'>
            { props.show.tagGroups[2].tags.map((x) => <li className='tag'>{x}</li>) }
        </ul>
    </div>
}

export default ContentInfo;