import { circMoreInfo, playBtn } from '../assets/SVGIcons';
import '../css/Showcase.css';

function Showcase(props) {
    return <div id='item-showcase'>
        <div id='showcase-preview'>
            <img src={props.show.poster} alt='showcase' />
            <div id='img-bottom-mask' />
        </div>
        <div id='showcase-billboard'>
            <h2 id='showcase-title'>{props.show.title}</h2>
            <div id='showcase-btns'>
                <button id='showcase-play-btn'>{playBtn} Play</button>
                <button id='showcase-info-btn' onClick={() => props.loadMoreInfo(props.show)}>{circMoreInfo} More Info</button>
            </div>
        </div>
    </div>
};

export default Showcase;