import '../css/MoreLikeThis.css';
import { addBtn } from '../assets/SVGIcons';
import { useState } from 'react';
import ExpandButton from './ExpandButton';
import showList from '../assets/ShowList';

function MoreLikeThis(props) {
    let [bExpanded, setBExpanded] = useState(false);
    const loadSuggestedShows = () => {
        if(!bExpanded)
        {
            return showList
                .filter((x, i) => i < 6)
                .map((show) => <SuggestedShow img={show.poster} title={show.title} date={show.date} desc={show.desc}/>)
        }
        else {
            return showList
                .map((show) => <SuggestedShow img={show.poster} title={show.title} date={show.date} desc={show.desc}/>)
        }
    }
    return <div id='more-like-this'>
        <h1>More Like This</h1>
        <div id='suggested-shows' className={ bExpanded ? '' : 'clip' }>
            { loadSuggestedShows() }
        </div>
        {
            <ExpandButton handleClick={setBExpanded} bExpanded={bExpanded}/>
        }
    </div>
}

function SuggestedShow(props) {
    return <div className='suggested-show'>
    <img src={props.img} alt={props.title}/>
    <div className='suggested-metadata'>
        <div className='metadata-left'>
            <h3>77% Match</h3>
            <h3>{props.date}</h3>
        </div>
        <button className='metadata-add-btn'>{addBtn}</button>
    </div>
    <p className='suggested-desc'>{props.desc}</p>
</div>
}

export default MoreLikeThis;