import { cloneElement, useState } from 'react';
import '../css/FeatureStrip.css';

const mod = function(val, n) {
    return ((val%n)+n)%n;
};

function FeatureStrip(props) {
    let childLength = props.children.length;
    let [classList, setClassList] = useState('');
    let [bStartedSwiping, setBStartedSwiping] = useState(false);
    let [bIsMoving, setBIsMoving] = useState(false);
    let [strip, setStrip] = useState({ min: 0, max: Math.min(5, childLength), transformX: 0});
    const incrementVal = 100 / 6; // 100% of space / amount of shows shown

    const handleNext = (e) => {
        if(bIsMoving) return;
        setBIsMoving(true);
        let parent = e.target.parentNode.children[0];
        let newXPos = strip.max === childLength - 1 
            ? 6
            : Math.min(Math.max((childLength - 1) - strip.max, 0), 6);
        let transformX = strip.transformX - (newXPos) * incrementVal;
        setClassList('anim');
        parent.style.transform = `translateX(${transformX}%)`;
        setTimeout(() => {
            setBStartedSwiping(true);
            setBIsMoving(false);
            setClassList('');
            parent.style.transform = `translateX(${-100}%)`;
            if(strip.max === childLength - 1)
                setStrip({ min: 0, max: 5, transformX: -100 });
            else
                setStrip({ min: strip.min + newXPos, max: strip.max + newXPos, transformX: -100 });
        }, 275); // maybe make timeout length * newPos amount so its more accurate
    }
    const handlePrevious = (e) => {
        if(bIsMoving) return;
        setBIsMoving(true);
        let parent = e.target.parentNode.children[0];
        let newXPos = strip.min === 0 
            ? 6
            : Math.min(Math.max(0 + strip.min, 0), 6);
        let transformX = strip.transformX + (newXPos) * incrementVal;
        setClassList('anim');
        parent.style.transform = `translateX(${transformX}%)`;
        setTimeout(() => {
            setBStartedSwiping(true);
            setBIsMoving(false);
            setClassList('');
            parent.style.transform = `translateX(${-100}%)`;
            if(strip.min === 0)
                setStrip({ min: childLength - 6, max: childLength - 1, transformX: -100 });
            else
                setStrip({ min: strip.min - newXPos, max: strip.max - newXPos, transformX: -100 });
        }, 275);
    }
    const createRow = (min, max) => {
        let elements = [];

        if(bStartedSwiping && childLength > 6)
        {
            for(let i = min - 6; i <= min - 1; i++){
                let child = props.children[mod(i, childLength)]; // todo neg
                if(child == null) continue;
                let elem = cloneElement(child, {...child.props, classes: 'previous-row', index: i, key: i, loadMoreInfo:props.loadMoreInfo});
    
                if(elem !== null) elements.push(elem);
            }
        }

        for(let i = min; i <= max; i++){
            let child = props.children[i];
            if(child == null) continue;
            let classes = 'current-row' + (i === min ? '-first' : i === max ? '-last' : ''); 
            let elem = cloneElement(child, {...child.props, classes: classes, index: i, key: i, loadMoreInfo:props.loadMoreInfo});

            if(elem !== null) elements.push(elem);
        }

        if(childLength > 6) {
            for(let i = max + 1; i <= max + 6; i++){
                let child = props.children[i % childLength];
                if(child == null) continue;
                let elem = cloneElement(child, {...child.props, classes: 'next-row', index: i, key: i, loadMoreInfo:props.loadMoreInfo});
    
                if(elem !== null) elements.push(elem);
            }
        }

        return elements;
    }

    return <div className='feature-strip'>
        <h1 className='feature-title'>{props.header}</h1>
        <div className='feature-area'>
            <div className={'feature-content ' + classList}>
                {console.log('refresh', strip.min - 6, strip.min, strip.max, strip.max + 6, childLength)}
                { createRow(strip.min, strip.max) }
            </div>
            {
                bStartedSwiping && 
                    <button className='prev-btn' onClick={(e) => handlePrevious(e)}>{'<'}</button>
            }
            {
                props.children.length > 6 && 
                    <button className='next-btn' onClick={(e) => handleNext(e)}>{'>'}</button>
            }
        </div>
    </div>
}

export default FeatureStrip;