import { useEffect, useState } from "react"
import ContentInfo from "./ContentInfo";
import '../css/ContentItem.css';

function ContentItem(props) {
    let [bIsSelected, setBIsSelected] = useState(false);
    let [selectTimeout, setSelectTimeout] = useState(null);

    useEffect(() => {
        return function cleanup() {
            clearTimeout(selectTimeout);
        };
    });

    if(bIsSelected)
    {
        return <div className={'content-item expanded ' + props.classes} id='selected-item'
            onMouseLeave={() => {
                setSelectTimeout(setTimeout(() => setBIsSelected(false), 50))}}>
            <img src={props.show.poster} className='content-img' alt ='content' />
            <ContentInfo loadMoreInfo={props.loadMoreInfo} show={props.show}/>
        </div>
    }

    return <div className={'content-item ' + props.classes + ' ' + props.index}
        onMouseEnter={() => 
            setSelectTimeout(setTimeout(() => setBIsSelected(true), 500))}
        onMouseLeave={() => clearTimeout(selectTimeout)}>
        <img src={props.show.poster} className='content-img' alt ='content' />
    </div>
}

export default ContentItem;