import { addBtn, dislikeBtn, likeBtn, playBtn } from '../assets/SVGIcons';
import '../css/PreviewModal.css';
import EpisodeSelector from './EpisodeSelector';
import MoreLikeThis from './MoreLikeThis';
import PreviewAbout from './PreviewAbout';

function PreviewModal(props) {
    let selectedShow = props.selectedShow;
    return <div className='darken-background' onClick={() => props.exitModal()}>
        <div id='preview-modal' onClick={(e) => e.stopPropagation()}>
            <div id='preview'>
                <button id='preview-close-btn' onClick={() => props.exitModal()} class='info-btn'>{ addBtn }</button>
                <img src={selectedShow.poster} alt='show preview' id='preview-img' />
                <div id='gradient' />
                <div id='preview-details'>
                    <h1 id='preview-text'>{ selectedShow.title }</h1>
                    <div className='detail-btns'>
                        <button className='info-btn square'><div>{playBtn}</div> Play</button>
                        <button className='info-btn'><div>{addBtn}</div></button>
                        <button className='info-btn'><div>{likeBtn}</div></button>
                        <button className='info-btn'><div>{dislikeBtn}</div></button>
                    </div>
                </div>
            </div>
            <div id='expanded-info'>
                <div id='details'>
                    <div id='details-left'>
                    <div id='expanded-metadata'>
                        <h5 id='expanded-compatibility'>98% Match</h5>
                        <h5 id='expanded-release-date'>{ selectedShow.date }</h5>
                        <h5 id='expanded-seasons'>{ selectedShow.type ==='TV'
                             ? selectedShow.seasons
                             : selectedShow.length }</h5>
                    </div> 
                    <p id='expanded-description'>
                        { selectedShow.desc }
                    </p>
                </div>
                    <div id='details-right'>
                    { 
                        selectedShow.tagGroups.map((x) => <div className='tag-group'>
                            {<span>{ x.title }</span>}
                            {x.tags.map((tag, i) => <a href='#tag'>{ tag + (i < x.tags.length - 1 ? ', ' : '') }</a>)}                            
                        </div>)
                    }
                </div>
                </div>
                { selectedShow.type === 'TV' && <EpisodeSelector thumbnail={selectedShow.poster} episodes={selectedShow.episodes}/> }
                <MoreLikeThis />
                <PreviewAbout />
            </div>
        </div>
    </div>
}

export default PreviewModal;