import '../css/EpisodeSelector.css';
import poster1 from '../assets/poster1.jpg';
import { useState } from 'react';
import ExpandButton from './ExpandButton';

function EpisodeSelector(props) {
    let [bExpanded, setBExpanded] = useState(false);
    let collapseHeight = 10;
    const loadEpisodes = () => {
        if(!bExpanded)
        {
            return props.episodes
            .filter((x,i) => i < collapseHeight)
            .map(x => 
            <TitleCard
                title={x.title} episode={x.index} thumbnail={props.thumbnail}
                duration={x.duration} description={x.summary}
            />)
        }
        else {
            return props.episodes
            .map(x => 
            <TitleCard
                title={x.title} episode={x.index} thumbnail={props.thumbnail}
                duration={x.duration} description={x.summary}
            />)
        }
    }
    return <div id='episode-selector'>
        <h1 id='episode-selector-header'>Episodes</h1>
        <div id='episode-selector-container'> {/* TODO -> add season dropdown */}
        </div>
        { loadEpisodes() }
        {
            props.episodes.length > collapseHeight && <ExpandButton handleClick={setBExpanded} bExpanded={bExpanded}/>
        }
    </div>
}

const TitleCard = (props) => {
    return <div className='episode-titlecard'>
    <div className='titlecard-index'>{props.episode}</div>
    <div className='titlecard-img'><img src={props.thumbnail} alt='' /></div>
        <div className='titlecard-text'>
            <div className='titlecard-title'>
                <span className='titlecard-title-text'>{props.title}</span>
                <span className='titlecard-length'>{props.duration}</span>
            </div>
            { props.description !== "" && <p className='titlecard-description'>{props.description}</p> }
        </div>
    </div>
}

export default EpisodeSelector;