import '../css/ExpandButton.css';

function ExpandButton(props) {
    let collapsed = props.bExpanded ? '' : 'collapsed';
    return <div className={'divider ' + collapsed}>
        <button onClick={() => props.handleClick(prevState => !prevState)} className='expand-btn'>
            { props.bExpanded ? '^' : 'v' }
        </button>
    </div>
}

export default ExpandButton;